  //valiadtion
  //email validation
  isValidEmail = (email) => {
    var re =
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  };

  //phone validation
  isValidPhone = (phone) => {
    var re = /^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$/;
    return re.test(phone);
  };

  //only numbers validation
  isValidOnlyNumber = (val) => {
    var re = /^[0-9]*$/;
    return re.test(val);
  };

  //map
  onMarkerDragEnd = (coord, index) => {
    const { latLng } = coord;
    const lat = latLng.lat();
    const lng = latLng.lng();

    this.setState((prevState) => {
      const markers = [...this.state.markers];
      markers[index] = { ...markers[index], position: { lat, lng } };
      return { markers };
    });
  };

  componentDidMount = () => {
    var data = localStorage.getItem("service_provider_id");
    var con = {
      id: data,
    };
    this.setState({ loading: true });
    fetch(api.apiUrl + "service_provider_personal_details", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify(con),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        if (responseJsonFromServerData.length !== 0) {
          responseJsonFromServerData.map((item, key) => {
            this.setState({
              BCContactPerson: item.firstName + " " + item.lastName,
              BCGarageName: item.shopName,
              BCPhoneNumber: item.mobileNumber,
              BCEmail: item.email,
              ABusinessType: item.businessType,
              AAddress1: item.address1,
              AAddress2: item.address2,
              AStreet: item.street,
              ACity: item.city,
              AState: item.state,
              AArea: item.areaId,
            });

            this.fetchAreaList(item.state);
            var lat = parseFloat(item.latitude);
            var lng = parseFloat(item.longitude);
            //to get map co-ordinates
            this.setState((prevState) => {
              const markers = [...this.state.markers];
              // console.log(markers);
              markers[0] = {
                ...markers[0],
                position: { lat, lng },
              };
              return { markers };
            });
          });
        } else {
          Toast.fail("failed...", 600, () => {
            this.props.history.push("/service-providers");
          });
        }
        this.setState({ loading: false });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
    //fetch states
    fetch(api.apiUrl + "get_states", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        this.setState({ states: responseJsonFromServerData });
        this.setState({ loading: false });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  };

