  handleChange = (input) => (e) => {
    // console.log(input);
    // console.log(e.target.value);
    this.setState({ [input]: e.target.value });
    this.setState({ [input + "Error"]: "" });

    if (input === "addServiceType") {
      this.fetchServiceList(e.target.value);
    } else if (input === "AState" && e.target.value !== "select") {
      this.fetchAreaList(e.target.value);
    }
  };

  fetchAreaList(id) {
    this.setState({ loading: true });
    var data = {
      StateId: id,
    };

    fetch(api.apiUrl + "get_areas", {
      method: "POST",
      // headers: authHeader(),
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        // console.log(responseJsonFromServerData);
        this.setState({
          areaArr: responseJsonFromServerData,
          loading: false,
          Area: this.state.AArea,
        });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

